<template>
  <v-app>
    <!-- App Bar -->
    <v-app-bar
      fixed
      dark
      app
    >
      <v-btn @click="$router.go(-1)" icon>
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
      <v-app-bar-title v-if="episode !== null">Detail Episode</v-app-bar-title>
      <v-skeleton-loader v-if="episode === null" type="heading" width="100%"></v-skeleton-loader>
    </v-app-bar>

    <!-- Main -->
    <v-main>
      <!-- Container -->
      <v-container fluid>
        <v-card v-if="title !== null && episode !== null && description !== null && server !== null">
          <v-card-title>Episode {{ episode }}</v-card-title>
          <v-card-subtitle>{{ title }}</v-card-subtitle>
          <v-card-text>{{ description }}</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>

            <router-link :to="{ path: '/anime/'+episode_id.split('-episode-')[0].replace('-movie', '') }">
              <v-btn>Detail Anime</v-btn>
            </router-link>
          </v-card-actions>
          <v-card-title>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Cari"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :headers="serverHeaders"
            :items="server"
            :search="search"
          >
            <template v-slot:item.url="{ item }">
              <a :href="item.url" target="_blank">
                <v-btn>
                  <v-icon class="mr-2">mdi-cloud-download</v-icon> Unduh
                </v-btn>
              </a>
            </template>
            <template v-slot:item.stream="{ item }">
              <a v-if="item.stream !== null" :href="item.stream" target="_blank">
                <v-btn>
                  <v-icon class="mr-2">mdi-play</v-icon> Tonton
                </v-btn>
              </a>
              <v-btn v-else disabled>
                <v-icon class="mr-2">mdi-play</v-icon> Tonton
              </v-btn>
            </template>
          </v-data-table>
        </v-card>
        <v-skeleton-loader v-if="server === null" type="article" width="100%"></v-skeleton-loader>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import Vue from "vue";
import axios from "axios";

export default Vue.extend({
  name: "Episode",
  data() {
    return {
      search: '',
      episode_id: this.$route.params.episode_id,
      title: null,
      episode: null,
      description: null,
      server: null,
      serverHeaders: [
        {
          text: "Kualitas",
          align: "start",
          shortable: false,
          value: "quality",
        },
        {
          text: "",
          align: "center",
          value: "url"
        },
        {
          text: "",
          align: "center",
          value: "stream"
        },
      ],
    }
  },
  async created() {
    await axios.get("https://api.abpay.xyz/api/episode?api_key=b988b287-c0ff-408c-b941-9619f283db76&episode_id="+this.episode_id)
      .then(async (res) => {
        const response = res.data;
        if (response.status === true) {
          this.title = response.data.title.replace(' Indo', '');
          this.episode = response.data.episode;
          this.description = response.data.description;
          var server = response.data.server;

          for (let i = 0; i < server.length; i++) {
            await axios.get("https://api.abpay.xyz/freeapi/embedvideo?server="+server[i].server+"&url="+server[i].url)
              .then(res2 => {
                const response2 = res2.data;
                if (response2 === server[i].url) {
                  server[i].stream = null;
                } else {
                  server[i].stream = response2;
                }
              }).catch(err2 => {
                console.log(err2);
              });
          }

          this.server = server;
        }
      }).catch(err => {
        console.log(err);
      });
  },
});
</script>